header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    justify-content: center;
    height: 6rem;
    text-align: center;
    background: #fff;

    .logo {
        width: 200px;
        height: 23px;
    }

    @media (max-height: 460px) {
        height: 4rem;

        .logo {
            width: 140px;
            height: 15px;
        }
    }
}
